import axios from 'axios';
import { config } from './config';

export default {
    /** Popular Twitter-trends */
    getTwitterTrends: () => {
        return fetch(config.baseApiUrl + 'api/twitter-trends').
            then((Response) => Response.json())
            .catch({ status: 500, message: 'Internal Server Error' });
    },

    /** Popular Twitter-tweets */
    getTwitterTweets: () => {
        return fetch(config.baseApiUrl + 'api/twitter-tweets').
            then((Response) => Response.json())
            .catch({ status: 500, message: 'Internal Server Error' });
    },

    /** 
     * @param {string} searchTweetsObj
     * Popular Search-Tweets
     */
    getSearchTweets: (searchTweetsObj) => {
        return axios.get(config.baseApiUrl + 'api/search-tweets', searchTweetsObj).then(response => {
            return response;
        })
            .catch({ status: 500, message: 'Internal Server Error' });
    },

    /** 
     * @param {string} hashtagObj
     * Add Hashtag
     */
    getHashtags: (hashtagObj) => {
        return axios.post(config.baseApiUrl + 'api/user/addtag', hashtagObj).then(response => {
            return response;
        })
            .catch({ status: 500, message: 'Internal Server Error' });
    },

    /** Display Hashtag */
    displayHashtag: () => {
        return axios.get(config.baseApiUrl + 'api/user/gethashtag/' + localStorage.getItem('email')).then((response) => {
            return response;
        })
            .catch({ status: 500, message: 'Internal Server Error' });
    },

    /**
     * @param {string} deleteHashtagObj
     * Delete Hashtag
     */
    deleteHashtag: (deleteHashtagObj) => {
        return axios.delete(config.baseApiUrl + 'api/user/deletehashtag', deleteHashtagObj).then((response) => {
            return response;
        })
            .catch({ status: 500, message: 'Internal Server Error' });
    },

    /**
     *  @param {string} updateHashtagObj
     *  Update Hashtag
     */
    updateHashtag: (updateHashtagObj) => {
        return axios.put(config.baseApiUrl + 'api/user/updatehashtag', updateHashtagObj).then((response) => {
            return response;
        })
            .catch({ status: 500, message: 'Internal Server Error' });
    },

    /**
     *  @param {string} token
     *  Authenticate with token
     */
    authenticate: () => {
        const Token = localStorage.getItem('token');
        const headers = {
            'Content-Type': 'application/json',
            'Authorization': 'JWT fefege...',
            'token': Token
        }
        return axios.get('https://immense-refuge-41641.herokuapp.com/api/user/checkToken', { headers: headers }).then((response) => {
            return response;
        })
            .catch({ status: 500, message: 'Internal Server Error' });
    }
}


